define('PHET_CORE/pairs',['require','PHET_CORE/core'],function (require) {
  'use strict';
  var core = require('PHET_CORE/core');
  core.pairs = function pairs(array) {
    var result = [];
    var length = array.length;
    if (length > 1) {
      for (var i = 0; i < length - 1; i++) {
        var first = array[i];
        for (var j = i + 1; j < length; j++) {
          result.push([
            first,
            array[j]
          ]);
        }
      }
    }
    return result;
  };
  return core.pairs;
});
