define('SCENERY/display/webgl/BlockItem',['require','PHET_CORE/inherit'],function (require) {
  'use strict';
  var inherit = require('PHET_CORE/inherit');
  function BlockItem(x, y, width, height) {
    this.x = x;
    this.y = y;
    this.width = width;
    this.height = height;
    this.used = false;
    this.position = null;
    this.right = null;
    this.down = null;
  }
  return inherit(Object, BlockItem);
});
