define('PHET_CORE/platform',['require','PHET_CORE/core'],function (require) {
  'use strict';
  var core = require('PHET_CORE/core');
  var ua = navigator.userAgent;
  function isIE(version) {
    return getInternetExplorerVersion() === version;
  }
  function getInternetExplorerVersion() {
    var rv = -1;
    var re = null;
    if (navigator.appName === 'Microsoft Internet Explorer') {
      re = new RegExp('MSIE ([0-9]{1,}[.0-9]{0,})');
      if (re.exec(ua) !== null) {
        rv = parseFloat(RegExp.$1);
      }
    } else if (navigator.appName === 'Netscape') {
      re = new RegExp('Trident/.*rv:([0-9]{1,}[.0-9]{0,})');
      if (re.exec(ua) !== null) {
        rv = parseFloat(RegExp.$1);
      }
    }
    return rv;
  }
  core.platform = {
    get firefox() {
      return ua.toLowerCase().indexOf('firefox') > -1;
    },
    get mobileSafari() {
      return ua.match(/(iPod|iPhone|iPad)/) && ua.match(/AppleWebKit/);
    },
    get safari5() {
      return ua.match(/Version\/5\./) && ua.match(/Safari\//) && ua.match(/AppleWebKit/);
    },
    get safari6() {
      return ua.match(/Version\/6\./) && ua.match(/Safari\//) && ua.match(/AppleWebKit/);
    },
    get safari7() {
      return ua.match(/Version\/7\./) && ua.match(/Safari\//) && ua.match(/AppleWebKit/);
    },
    get ie9() {
      return isIE(9);
    },
    get ie10() {
      return isIE(10);
    },
    get ie11() {
      return isIE(11);
    },
    get ie() {
      return getInternetExplorerVersion() !== -1;
    },
    get android() {
      return ua.indexOf('Android') > 0;
    },
    get chromium() {
      return /chrom(e|ium)/.test(ua.toLowerCase());
    }
  };
  return core.platform;
});
