define('PHET_CORE/profiler',['require','PHET_CORE/core'],function (require) {
  'use strict';
  var core = require('PHET_CORE/core');
  var stack = [];
  var results = {};
  var count = 0;
  var listeners = [];
  core.profiler = {
    displayCount: 1000,
    start: function (name) {
      var time = Date.now();
      stack.push({
        name: name,
        time: time
      });
    },
    addListener: function (listener) {
      listeners.push(listener);
    },
    stop: function () {
      var end = Date.now();
      var top = stack.pop();
      var elapsed = end - top.time;
      if (!results[top.name]) {
        results[top.name] = [];
      }
      results[top.name].push(elapsed);
      count++;
      if (count % this.displayCount === 0) {
        var summary = JSON.stringify(this.toJSON());
        console.log(summary);
        for (var i = 0; i < listeners.length; i++) {
          listeners[i](summary);
        }
        results = {};
      }
    },
    toJSON: function () {
      var summary = {};
      var sum;
      for (var property in results) {
        sum = 0;
        for (var i = 0; i < results[property].length; i++) {
          var time = results[property][i];
          sum += time;
        }
        var average = sum / results[property].length;
        summary[property] = {
          average: average,
          count: results[property].length
        };
      }
      return summary;
    },
    testSelf: function () {
      var a, b;
      var profiler = this;
      this.displayCount = 10000000;
      for (var i = 0; i < 10; i++) {
        profiler.start('physics');
        for (var k = 0; k < 10000; k++) {
          profiler.start('mloop');
          for (var m = 0; m < 10000; m++) {
            a = 100 * 200;
          }
          profiler.stop();
          profiler.start('xloop');
          for (var x = 0; x < 20000; x++) {
            b = 100 * 200;
          }
          profiler.stop();
        }
        profiler.stop();
      }
      console.log('results: ', a, b);
      console.log(JSON.stringify(this.toJSON()));
    }
  };
  return core.profiler;
});
